const exec = require('@actions/exec');

const getCommitsSinceTag = async(tag, dir) => {
  const format = {hash: '%H', date: '%aI', header: '%s', body: '%b', author: { name: '%an', email: '%ae'}};
  const gitLogFormat = JSON.stringify(format).replace(/"/gm, '%x1A');

  const range = tag ? `${tag}..HEAD` : "";

  let out = '';
  let errors = '';

  const options = {
    listeners: {
      stdout: (data) => {
        out += data.toString();
      },
      stderr: (error) => {
        errors += error.toString();
      }
    },
    failOnStdErr: true,
    silent: true,
  }
  options.cwd = dir || process.env.GITHUB_WORKSPACE || process.cwd();

  if ((await exec.exec('git', ['--no-pager', 'log', range, '--no-merges', `--pretty=format:${gitLogFormat}`], options)) === 0) {
    // eslint-disable-next-line no-control-regex
    return JSON.parse(`[${out.replace(/\x1A/gm, '"')}]`);
  } else {
    console.log(errors);
    return null;
  }
}

module.exports = { getCommitsSinceTag }