const exec = require('@actions/exec');

const getTags = async(dir) => {
  let out = '';
  let errors = '';

  const options = {
    listeners: {
      stdout: (data) => {
        out += data.toString();
      },
      stderr: (error) => {
        errors += error.toString();
      }
    },
    failOnStdErr: true,
    silent: true,
  }
  options.cwd = dir || process.env.GITHUB_WORKSPACE || process.cwd();

  if ((await exec.exec('git', ['--no-pager','tag', '-l', '--sort=-v:refname'], options)) === 0) {
    return out.split(/\r?\n/);
  } else {
    console.log(errors);
    return null;
  }
}

module.exports = {getTags}